

#main {
    padding: 40px;
    display: flex;
}

/*
 *  Side menu
 */

#sidemenu {
    border: solid gray 1px;
    border-radius: 5px;
    margin-bottom: auto;
    width: 300px;
    text-align: center;
}

#sidemenu ul {
    padding: 0px;
}

#sidemenu li {
    margin: 15px;
    list-style-type: none;
    cursor: pointer;
}

#sidemenu li:hover {
    color: green;
}

/*
 *  Content
 */

#content {
    margin-left: 40px;
}

#distinct-io {
    margin-left: 40px;
}

#input-separater {
    resize: none;
}

#output-separater {
    resize: none;
}

#input {
    width: 40%;
}

#output {
    width: 40%;
}

#duplicate-statistics {
    margin-top: 20px;
    margin-left: 40px;
    width: 80%;
    border-collapse: collapse ;
    text-align: center;
}

#duplicate-statistics th{
    border: 1px gray solid;
    border-spacing: 0ch;
    font-weight: normal;
}

#duplicate-statistics td{
    border: 1px gray solid;
    border-spacing: 0ch;
}